@import "../../Styles/main.scss";

.quotes-section {
  background-color: $color-bg-light;
}

.print {
  padding: $padding;
}

.print-component {
  height: 0;
  visibility: hidden;
  z-index: -1;
}

.utilities {
  &__container {
    display: flex;
    flex-flow: row wrap;
    justify-content: space-around;
    align-items: center;
    align-content: stretch;
    margin-bottom: $spacer-med;
    font-size: $default-3;
  }

  &__button {
    text-transform: uppercase;
    background-color: $color-light-blue;
    color: $color-green;
    border-radius: $round-edges;
    padding: $padding-button;
    min-width: 20rem;
    font-weight: bolder;
    margin: 0.5rem 0;
    text-align: center;
    cursor: pointer;
  }
}

.quotes {
  &__title {
    font-size: $heading-3;
    text-transform: uppercase;
    margin-bottom: $spacer-med;
  }

  &__list {
  }

  &__item {
    list-style: none;
    margin-bottom: $spacer-med;
    font-size: $default-3;
  }
}
