@import "../../../../Styles/main.scss";

.themes-section {
  background-color: $color-bg-light;
}

.select-theme {
  text-align: center;
  max-width: 25rem;
  margin: 0 auto;

  &__title {
    font-size: $heading-4;
    margin-bottom: $spacer-small;
    text-transform: uppercase;
  }

  &__button {
    background-color: $color-dark-blue;
    color: $color-text-light;
    padding: $padding-button;
    border-radius: $round-edges;
    text-transform: uppercase;
    font-weight: bolder;
    font-size: $default-3;
    margin-bottom: $spacer-small;
    cursor: pointer;
  }

  &__generate-button {
    display: inline-block;
    background-color: $color-green;
    border-radius: $round-edges;
    padding: $padding-button;
    color: $color-text-light;
    font-size: $default-3;
    font-weight: bolder;
    text-transform: uppercase;
    cursor: pointer;
  }
}

.select-theme-modal {
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100vw;
  overflow-y: scroll;
  background-color: $color-bg-light;

  &__content {
    max-width: 60rem;
    margin: 2rem auto;
  }

  &__search-bar {
  }
}
