*,*::after,*::before{margin:0;padding:0;box-sizing:inherit;font-weight:200}html{font-size:100%}body{font-family:"Montserrat", sans-serif;color:#393D44;box-sizing:border-box;max-width:60rem;padding:1rem 2rem;margin:0 auto;background-color:#FFF9F0;overflow-x:hidden;overflow-y:scroll}p{font-family:"Comic Neue", cursive;font-weight:300}a,a:hover,a:focus,a:active{text-decoration:none;color:inherit}

.header{background-color:#FFF9F0;text-align:center}.header__title{font-size:calc(min(9vw, 4rem));margin-bottom:3.5rem}.navigation{text-align:left;margin:0 auto}.navigation__back-button{display:inline-block;background-color:#00D1CB;padding:0.5rem 0.75rem;border-radius:2000px;color:#fff}

.footer{background-color:#FFF9F0;padding:2rem 3rem;text-align:center}

.landing-section{background-color:#FFF9F0}.description{font-size:1.7rem;margin:0 auto;margin-bottom:2.5rem;text-align:center}.toggle-button__container{display:flex;flex-flow:row nowrap;justify-content:center;align-items:center;align-content:stretch;margin-bottom:2.5rem}.toggle-button__button{margin:0 2rem}

.toggle{-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;position:relative;height:2.5rem;width:7.5rem;margin:0 auto}.toggle__label{display:block;text-align:center;font-size:2rem;margin-bottom:0.5rem}.toggle__bg{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);background-color:#7e8381;width:70%;height:70%;border-radius:2000px;transition:all 0.3s ease}.toggle__bg--active{background-color:#07455F}.toggle__circle{height:2.5rem;width:2.5rem;border-radius:2000px;position:absolute;left:0;transform:translate(30%);background-color:#4c4f4e;transition:all 0.3s ease-out}.toggle__circle--active{left:100%;transform:translate(-130%);background-color:#00D1CB}

.generate-section{background-color:#FFF9F0}.generate{text-align:center;max-width:25rem;margin:0 auto;margin-bottom:2.5rem}.generate__title{font-size:2rem;margin-bottom:1.5rem;text-transform:uppercase}.generate__selection-container{width:calc(min(100%, 35rem));margin:0 auto}

.list__item{color:#fff;font-size:1.5rem;font-weight:bolder;text-transform:uppercase;background-color:#00D1CB;border-radius:2000px;padding:0.75rem 1.5rem;text-align:center;width:100%;margin:1rem auto;cursor:pointer}.list__item--clicked{background-color:#07455F}

.themes-section{background-color:#FFF9F0}.select-theme{text-align:center;max-width:25rem;margin:0 auto}.select-theme__title{font-size:2rem;margin-bottom:1.5rem;text-transform:uppercase}.select-theme__button{background-color:#07455F;color:#fff;padding:0.75rem 1.5rem;border-radius:2000px;text-transform:uppercase;font-weight:bolder;font-size:1.5rem;margin-bottom:1.5rem;cursor:pointer}.select-theme__generate-button{display:inline-block;background-color:#00D1CB;border-radius:2000px;padding:0.75rem 1.5rem;color:#fff;font-size:1.5rem;font-weight:bolder;text-transform:uppercase;cursor:pointer}.select-theme-modal{position:fixed;top:0;left:0;height:100vh;width:100vw;overflow-y:scroll;background-color:#FFF9F0}.select-theme-modal__content{max-width:60rem;margin:2rem auto}

.pill-list{display:flex;flex-flow:row wrap;justify-content:start;align-items:center;align-content:stretch}.pill-list__item{color:#fff;font-size:1.5rem;font-weight:500;background-color:#00D1CB;border-radius:2000px;padding:0.75rem 1.5rem;margin:0.5rem;text-align:center;cursor:pointer}.pill-list__item--clicked{background-color:#07455F}

.search{text-align:center;margin-bottom:1.5rem}.search__input{font-size:1rem;outline:none;border:0px solid;padding:0.75rem 1.5rem;border-radius:2000px;width:20rem;margin:0 auto;background-color:#E0FFFB;color:#00D1CB}.search__input::-webkit-input-placeholder{color:#00D1CB}.search__input:-ms-input-placeholder{color:#00D1CB}.search__input::-ms-input-placeholder{color:#00D1CB}.search__input::placeholder{color:#00D1CB}

.gamsat-section{background-color:#FFF9F0}.gamsat__container{width:calc(min(100%, 35rem));margin:0 auto;max-width:25rem}

.quotes-section{background-color:#FFF9F0}.print{padding:2rem 3rem}.print-component{height:0;visibility:hidden;z-index:-1}.utilities__container{display:flex;flex-flow:row wrap;justify-content:space-around;align-items:center;align-content:stretch;margin-bottom:2.5rem;font-size:1.5rem}.utilities__button{text-transform:uppercase;background-color:#E0FFFB;color:#00D1CB;border-radius:2000px;padding:0.75rem 1.5rem;min-width:20rem;font-weight:bolder;margin:0.5rem 0;text-align:center;cursor:pointer}.quotes__title{font-size:3rem;text-transform:uppercase;margin-bottom:2.5rem}.quotes__item{list-style:none;margin-bottom:2.5rem;font-size:1.5rem}

.hidden-button{-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;display:inline-block;position:relative;font-size:2rem;display:flex;flex-flow:row wrap;justify-content:left;align-items:center;align-content:stretch;margin:0 auto}.hidden-button__card{perspective:50rem}.hidden-button__card__item{transition:all 0.5s ease-out;padding:0.5rem 1rem;font-size:1.25rem;border-radius:2000px;text-align:center;color:#00D1CB;background-color:#FFE5C5;white-space:nowrap;cursor:pointer}.hidden-button__card__item__visible{position:absolute;-webkit-backface-visibility:hidden;backface-visibility:hidden;transform:translateY(-50%)}.hidden-button__card__item__visible--clicked{transform:translateY(-50%) rotateX(180deg)}.hidden-button__card__item__hidden{position:absolute;-webkit-backface-visibility:hidden;backface-visibility:hidden;transform:translateY(-50%) rotateX(-180deg)}.hidden-button__card__item__hidden--clicked{transform:translateY(-50%) rotateX(0deg)}

.timer{text-align:center;width:20rem;margin:0 auto;color:#fff;margin-bottom:1.5rem}.timer__display{-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;background-color:#00D1CB;padding:0.75rem 1.5rem;border-radius:2000px;font-size:3rem;font-weight:bolder;margin-bottom:1.5rem}.timer__control{display:flex;flex-flow:row nowrap;justify-content:space-evenly;align-items:center;align-content:stretch;margin:0 auto}.timer__item{-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;background-color:#00D1CB;padding:0.75rem 1.5rem;border-radius:2000px;margin:0 0.5rem;text-transform:uppercase;font-size:1rem;font-weight:bolder;cursor:pointer}

