@import "../../Styles/main.scss";

.pill-list {
  display: flex;
  flex-flow: row wrap;
  justify-content: start;
  align-items: center;
  align-content: stretch;

  &__item {
    color: $color-text-light;
    font-size: $default-3;
    font-weight: 500;
    background-color: $color-green;
    border-radius: $round-edges;
    padding: $padding-button;
    margin: 0.5rem;
    text-align: center;
    cursor: pointer;

    &--clicked {
      background-color: $color-dark-blue;
    }
  }
}
